<template>
  <div>
    <van-nav-bar title="激活优惠卷" left-arrow @click-left="$router.go(-1)" />
    <div class="box">
      <div class="activate-box">
        <input type="text" placeholder="请输入优惠券激活码" v-model="code" />
      </div>
      <div class="btn" @click="add" :style="{background:color}">确定</div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            code:'',
            color:'#dbdbdb'
        }
    },
    methods:{
        add(){
            let list = this.$store.state.coilingCodeList
            list.forEach((item) => {
                if (item.coilingCode==this.code&&item.stact) {
                    this.$store.state.myjp.useCoupon.push(item)
                    this.$router.push("/discount")
                    item.stact = false
                    return
                }
                if (item.coilingCode==this.code&&!item.stact) {
                    Toast("已经领取过了");
                }
                  if (item.coilingCode!==this.code) {
                    Toast("您输入的卷码有误");
                    this.code=''
                }
            });
        }
    },
    watch: {
        code(newVal){
            if (newVal.length>0) {
                this.color = '#ff464e'
            }
        }
    },
};
</script>

<style lang="scss" scoped>
.box {
  padding-right: 12px;
  padding-left: 12px;
  .activate-box {
    margin-top: 20px;
    margin-bottom: 25px;
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #ebebeb;
    input {
      width: 100%;
      height: 18px;
      line-height: 18px;
      border: 0;
      font-size: 14px;
      padding-left: 15px;
      padding-right: 15px;
    }
  }
  .btn {
      height: 45px;
      line-height: 45px;
      font-size: 18px;
      background: #dbdbdb;
      margin-bottom: 30px;
      color: #fff;
      text-align: center
    }
}
</style>